"use client";

import { User } from "lucide-react";
import { useAuth } from "../context/AuthContext";
import Link from "next/link";

const Horoscope = () => {
  const { user , isAuthenticated } = useAuth();

  if (isAuthenticated){
  if (
    user?.dateOfBirth &&
    user?.birthTime &&
    user?.latitude &&
    user?.longitude &&
    user?.gender &&
    user?.whatsapp
  ) {
    const indexses = {
      1: { 1: "ගු", 2: "ගු ", 3: "ගු" },
      2: { 1: "ගු", 2: "ගු ", 3: "ගු", 4: "ගු" },
      3: { 1: "ගු", 2: "ගු ", 3: "ගු", 4: "ගු", 5: "ගු" },
      4: { 1: "ගු", 2: "ගු ", 3: "ගු" },
      5: { 1: "ගු", 2: "ගු ", 3: "ගු" },
      6: { 1: "ගු", 2: "ගු ", 3: "ගු" },
      7: { 1: "ගු", 2: "ගු ", 3: "ගු" },
      8: { 1: "ගු", 2: "ගු ", 3: "ගු" },
      9: { 1: "ගු", 2: "ගු ", 3: "ගු" },
      10: { 1: "ගු", 2: "ගු ", 3: "ගු" },
      11: { 1: "ගු", 2: "ගු ", 3: "ගු" },
      12: { 1: "ගු", 2: "ගු ", 3: "ගු" },
    };

    const mahadashaItems = ["Sun (Surya)"];

    const antardashaItems = [
      "Venus/Venus",
      "Venus/Sun",
      "Venus/Moon",
      "Venus/Mars",
      "Venus/Rahu",
      "Venus/Jupiter",
      "Venus/Saturn",
      "Venus/Mercury",
      "Venus/Ketu",
    ];
    return (
      <div className="w-full flex-col lg:flex-row flex justify-center items-center gap-2">
        <div className="lg:w-[50%] w-full flex justify-center lg:mt-0 mt-4">
          <div className="border grid grid-cols-3 lg:w-[80%] lg:h-[80%] md:w-[60%] md:h-[60%] w-[90%] h-[90%] aspect-square">
            <div className="border relative aspect-square">
              <div className="absolute top-2 funnel-sans-text right-2 md:text-lg gap-2 lg:text-xl font-semibold grid grid-cols-2 text-sm">
                {Object.entries(indexses[2])?.map(([key, value]) => (
                  <span key={key}>{value}</span>
                ))}
              </div>

              <div className="absolute bottom-2 left-2 text-sm  lg:text-xl md:text-lg font-semibold funnel-sans-text grid grid-cols-2 gap-1">
                {Object.entries(indexses[1]).map(([key, value]) => (
                  <span key={key}>{value}</span>
                ))}
              </div>

              <svg
                className="absolute inset-0 w-full h-full"
                preserveAspectRatio="none"
              >
                <line
                  x1="0"
                  y1="0"
                  x2="100%"
                  y2="100%"
                  stroke="black"
                  strokeWidth="2"
                  vectorEffect="non-scaling-stroke"
                />
              </svg>
            </div>

            <div className="border aspect-square flex gap-2  justify-center items-center funnel-sans-text text-sm md:text-lg lg:text-xl font-semibold">
              <div className="grid grid-cols-2 ">
                {Object.entries(indexses[3]).map(([key, value]) => (
                  <span key={key} className="px-2 py-1">
                    {value}
                  </span>
                ))}
              </div>
            </div>

            <div className="border relative aspect-square">
              <div className="absolute top-2 left-2  text-sm md:text-lg font-semibold grid grid-cols-2 lg:text-xl">
                {Object.entries(indexses[4]).map(([key, value]) => (
                  <span key={key} className="px-2 py-1 funnel-sans-text">
                    {value}
                  </span>
                ))}
              </div>

              <div className="absolute bottom-0 md:bottom-2 md:right-2  right-0 lg:text-xl md:text-lg text-sm grid grid-cols-2 gap-1 font-semibold  funnel-sans-text">
                {Object.entries(indexses[5]).map(([key, value]) => (
                  <span key={key} className="px-2 py-1">
                    {value}
                  </span>
                ))}
              </div>

              <svg
                className="absolute inset-0 w-full h-full"
                preserveAspectRatio="none"
              >
                <line
                  x1="100%"
                  y1="0"
                  x2="0"
                  y2="100%"
                  stroke="black"
                  strokeWidth="2"
                  vectorEffect="non-scaling-stroke"
                />
              </svg>
            </div>

            <div className="border aspect-square flex justify-center funnel-sans-text text-sm items-center md:text-lg lg:text-xl font-semibold">
              <div className="grid grid-cols-2 ">
                {Object.entries(indexses[6]).map(([key, value]) => (
                  <span key={key} className="px-2 py-1">
                    {value}
                  </span>
                ))}
              </div>
            </div>

            <div className="border-2 aspect-square flex flex-col gap-2 funnel-sans-text justify-center items-center text-sm md:text-lg lg:text-xl font-bold">
              <span>මිථුන</span>
              <span>ලග්නය</span>
            </div>

            <div className="border aspect-square flex justify-center funnel-sans-text items-center lg:text-xl md:text-lg font-semibold text-sm">
              <div className="grid grid-cols-2 ">
                {Object.entries(indexses[7]).map(([key, value]) => (
                  <span key={key} className="px-2 py-1">
                    {value}
                  </span>
                ))}
              </div>
            </div>

            <div className="border relative aspect-square">
              <div className="absolute top-2 left-2 text-sm lg:text-xl md:text-lg  font-semibold grid grid-cols-2 ">
                {Object.entries(indexses[8]).map(([key, value]) => (
                  <span key={key} className="px-2 py-1 funnel-sans-text">
                    {value}
                  </span>
                ))}
              </div>

              <div className="absolute bottom-0 right-0 text-sm lg:text-xl md:text-lg funnel-sans-text font-semibold grid grid-cols-2">
                {Object.entries(indexses[9]).map(([key, value]) => (
                  <span key={key} className="px-2 py-1">
                    {value}
                  </span>
                ))}
              </div>

              <svg
                className="absolute inset-0 w-full h-full"
                preserveAspectRatio="none"
              >
                <line
                  x1="0"
                  y1="100%"
                  x2="100%"
                  y2="0"
                  stroke="black"
                  strokeWidth="2"
                  vectorEffect="non-scaling-stroke"
                />
              </svg>
            </div>

            <div className="border aspect-square flex justify-center items-center lg:text-xl md:text-lg text-sm  font-semibold funnel-sans-text">
              <div className="grid grid-cols-2 ">
                {Object.entries(indexses[10]).map(([key, value]) => (
                  <span key={key} className="px-2 py-1">
                    {value}
                  </span>
                ))}
              </div>
            </div>

            <div className="border relative aspect-square">
              <div className="absolute bottom-0 md:bottom-2 md:left-2 left-0  md:text-lg text-sm lg:text-xl font-semibold grid grid-cols-2">
                {Object.entries(indexses[11]).map(([key, value]) => (
                  <span key={key} className="px-2 py-1 funnel-sans-text">
                    {value}
                  </span>
                ))}
              </div>

              <div className="absolute top-0 md:top-2 md:right-2 right-0 lg:text-xl text-sm md:text-lg grid grid-cols-2 font-semibold">
                {Object.entries(indexses[12]).map(([key, value]) => (
                  <span key={key} className="px-2 py-1">
                    {value}
                  </span>
                ))}
              </div>

              <svg
                className="absolute inset-0 w-full h-full"
                preserveAspectRatio="none"
              >
                <line
                  x1="0"
                  y1="0"
                  x2="100%"
                  y2="100%"
                  stroke="black"
                  strokeWidth="2"
                  vectorEffect="non-scaling-stroke"
                />
              </svg>
            </div>
          </div>
        </div>
        <div className="w-full flex justify-center  lg:w-[50%] mt-4">
          <div className="flex flex-col gap-8 w-[90%]">
            {/* Mahadasha Card */}
            <div className="group">
              <div className="bg-white rounded-2xl shadow-xl hover:shadow-2xl transition-all duration-300 overflow-hidden border border-purple-100">
                <div className="bg-linear-to-r from-purple-600 to-indigo-600 p-6">
                  <h2 className="text-2xl font-bold text-white tracking-wide">
                    මහාදශාව
                  </h2>
                  <div className="h-1 w-20 bg-white/30 rounded-full mt-2"></div>
                </div>

                <div className="p-6">
                  <ul className="space-y-3">
                    {mahadashaItems.map((item, index) => (
                      <li
                        key={index}
                        className="flex items-center gap-3 p-3 rounded-lg hover:bg-purple-50 transition-colors duration-200 group/item"
                      >
                        <div className="w-2 h-2 rounded-full bg-linear-to-r from-purple-600 to-indigo-600 group-hover/item:scale-150 transition-transform duration-200"></div>
                        <span className="text-gray-700 group-hover/item:text-purple-700 transition-colors duration-200">
                          {item}
                        </span>
                      </li>
                    ))}
                  </ul>
                </div>
              </div>
            </div>

            {/* Antardasha Card */}
            <div className="group">
              <div className="bg-white rounded-2xl shadow-xl hover:shadow-2xl transition-all duration-300 overflow-hidden border border-indigo-100">
                <div className="bg-linear-to-r from-indigo-600 to-purple-600 p-6">
                  <h2 className="text-2xl font-bold text-white tracking-wide">
                    අන්තර්දශාව
                  </h2>
                  <div className="h-1 w-20 bg-white/30 rounded-full mt-2"></div>
                </div>

                <div className="p-6">
                  <ul className="space-y-3">
                    {antardashaItems.map((item, index) => (
                      <li
                        key={index}
                        className="flex items-center gap-3 p-2 rounded-lg hover:bg-indigo-50 transition-colors duration-200 group/item"
                      >
                        <div className="w-2 h-2 rounded-full bg-linear-to-r from-indigo-600 to-purple-600 group-hover/item:scale-150 transition-transform duration-200"></div>
                        <span className="text-gray-700 group-hover/item:text-indigo-700 transition-colors duration-200">
                          {item}
                        </span>
                      </li>
                    ))}
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  } else {
    return (
      <div className="min-h-screen bg-white flex items-center justify-center p-4">
        <div className="w-full max-w-md bg-gradient-to-br from-blue-600 via-indigo-700 to-black rounded-2xl shadow-2xl p-8">
          <div className="flex justify-center mb-6">
            <div className="bg-white rounded-full p-4 shadow-lg">
              <User className="w-16 h-16 text-indigo-700" strokeWidth={2} />
            </div>
          </div>

          <h1 className="text-white text-3xl font-bold text-center mb-2">
            Please Update Your Profile
          </h1>

          <p className="text-blue-100 text-center mb-4">Go to Profile</p>

          <Link href={"/profile"}>
            <button className="w-full bg-white text-indigo-700 font-semibold py-4 px-6 rounded-xl hover:bg-blue-50 transition-all duration-300 shadow-lg hover:shadow-xl transform hover:scale-105">
              Go to Profile
            </button>
          </Link>
        </div>
      </div>
    );
  }
}
};

export default Horoscope;
